import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import React from "react";
import { 
  Button,
  Container,
  Row,
  Col
} from "react-bootstrap";
import komaba from "./assets/komaba.jpeg";
import logo from "./assets/logo.png";
import BootstrapSwitchButton from 'bootstrap-switch-button-react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCalendarDay, faAppleAlt, faBullhorn, faFlask, faGlobe, faGraduationCap, faBriefcase, faChalkboardTeacher } from '@fortawesome/free-solid-svg-icons'
import { faUsers } from '@fortawesome/free-solid-svg-icons'

import { Schedule } from "./Schedule";

export default class App extends React.Component {
  constructor(props){
    super(props);
    this.state={
      mode:"time"
    }
  }

  renderIcon(key){
    switch(key){
      case "food": return faAppleAlt;
      case "media": return faBullhorn;
      case "science": return faFlask;
      case "international": return faGlobe;
      case "education": return faGraduationCap;
      case "business": return faBriefcase;
      case "semi": return faChalkboardTeacher;

    }
  }

  renderGenreSchedule(){
    let container = []
    for (const [key, val] of Object.entries(Schedule)){
      let group = Schedule[key].group;
      let groups = [];
      for(let i = 0; i < group.length; i++){
        let content = group[i];
        let contents = [];
        for(let j = 0; j < content.content.length; j++){
          contents.push(
            <>
              <div className="d-flex flex-row justify-content-between">
                <p className="align-self-center" style={styles.genre_fac}>{content.content[j].name}</p>
                <div className="d-flex flex-row">
                  <button className="genre_intro">紹介ページ</button>
                  <button className="genre_zoom">zoomに参加</button>
                </div>
              </div>
              <hr style={{backgroundColor: "rgba(255,255,255,0.25)", height:1}}/>
            </>
          )
        }
        let f_time = [
          <p style={styles.genre_date}>4/4</p>
        ]
        content["time"][0].forEach(date=>{
          let hm = date.split(":");
          let start = new Date(`2021-04-04T${hm[0]}:${hm[1]}:00+0900`);
          let end = new Date(`2021-04-04T${hm[0]}:${hm[1]}:00+0900`);
          end.setMinutes(end.getMinutes()+20)
          f_time.push(<p style={styles.genre_time}>{
            `${start.getHours()}:${start.getMinutes() === 0 ? "00" : start.getMinutes()} 〜 ${end.getHours()}:${end.getMinutes() === 0 ? "00" : end.getMinutes()}`
          }</p>)
        })
        let s_time = []
        if(content["time"].length===2){
          s_time.push(<p style={styles.genre_date}>4/11</p>)
          content["time"][1].forEach(date=>{
            let hm = date.split(":");
            let start = new Date(`2021-04-04T${hm[0]}:${hm[1]}:00+0900`);
            let end = new Date(`2021-04-04T${hm[0]}:${hm[1]}:00+0900`);
            end.setMinutes(end.getMinutes()+20)
            s_time.push(<p style={styles.genre_time}>{
              `${start.getHours()}:${start.getMinutes() === 0 ? "00" : start.getMinutes()} 〜 ${end.getHours()}:${end.getMinutes() === 0 ? "00" : end.getMinutes()}`
            }</p>)
          })
        }
        groups.push(
          <>
            <p className="genre_group">Group{i+1}</p>
            <Row style={{marginBottom:20}}>
              <Col md={4}>
                <div className="d-flex flex-row">
                  <div>
                    {f_time}
                  </div>
                  <div>
                    {s_time}
                  </div>
                </div>
              </Col>
              <Col md={8}>
                {contents}
              </Col>
            </Row>
          </>
        )
      }
      container.push(
        <div>
          <div className="d-flex flex-row" style={styles.genre_header}>
            <FontAwesomeIcon icon={this.renderIcon(key)} color="white" style={styles.genre_icon}/>
            <h3 style={styles.genre_title}>{Schedule[key]["title"]}</h3>
          </div>
          {groups}
        </div>
      )
    }
    return container;
  }

  render(){
    return (
      <body className="body">
        <header style={styles.header}>
          <div style={styles.header_overlay}>
            <Container>
              <img src={logo} style={styles.header_logo}/>
              <h1 className="header_title">UT-BASE主催<br/>オンライン合同新歓</h1>
              <p className="header_caption">
                UT-BASEは皆さんが「夢中になれる場所」を探すお手伝いをするため、統合的なオンライン新歓を実施してきました。<br/>
                そしてUT-BASEはこの度、春のオンライン合同新歓を開催します。<br/>
                参加方法は簡単！紹介ページを見て気になった団体の「説明会に参加」ボタンをクリックしてZoomに接続するだけ。<br/>
                さぁ、東大で最高の出会いを見つけよう。
              </p>
              <div className="d-flex flex-row">
                <div>
                  <div style={styles.date_head}>第一回</div>
                  <div style={styles.date_box}>4/4</div>
                </div>
                <div style={{marginLeft:16}}>
                  <div style={styles.date_head}>第二回</div>
                  <div style={styles.date_box}>4/11</div>
                </div>
              </div>
            </Container>
          </div>
        </header>
        <main style={styles.main}>
          <Container>
            <div className="d-flex flex-row">
              <div className={`d-flex flex-row mode_switch ${this.state.mode === "time" ? "underline" : "grayed"}`} style={{marginRight:32}} onClick={()=>{this.setState({mode:"time"})}}>
                <FontAwesomeIcon icon={faCalendarDay} color={"white"} style={styles.mode_icon}/>
                <p style={styles.mode_text}>開催時間別に表示</p>
              </div>
              <div className={`d-flex flex-row mode_switch ${this.state.mode === "each" ? "underline" : "grayed"}`} onClick={()=>{this.setState({mode:"each"})}}>
                <FontAwesomeIcon icon={faUsers} color={"white"} style={styles.mode_icon}/>
                <p style={styles.mode_text}>団体別に表示</p>
              </div>
            </div>
          </Container>
          {
            this.state.mode === "time" ? 
            <Container>
              <div style={styles.main_container}>
                <h2 style={styles.main_title}>タイムライン</h2>
              </div>
              <div style={styles.main_container}>
                <h2 style={styles.main_title}>ジャンル別スケジュール</h2>
                {this.renderGenreSchedule()}
              </div>
            </Container> : 
            <Container>
              <div style={styles.main_container}>
                <h2 style={styles.main_title}>団体を検索</h2>
              </div>
            </Container>
          }
        </main>
      </body>
    )
  };
}

const styles = {
  header:{
    backgroundImage:`url(${komaba})`,
    backgroundSize:"cover",
    height:600,
    backgroundPosition:"center",
  },
  header_overlay:{
    backgroundColor:"rgba(0,51,19,0.5)",
    height:600,
    width:"100%",
  },
  main:{
    marginTop:50
  },
  header_logo:{
    height:40,
    marginTop:16,
  },
  date_head:{
    color:"#00C0AB",
    width:80,
    height:16,
    backgroundColor:"rgba(0,0,0,0.7)",
    borderRadius:8,
    fontSize:8,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    marginBottom:4,
    fontWeight:"bold",
    marginTop:32,
  },
  date_box:{
    color:"white",
    width:80,
    height:80,
    backgroundColor:"rgba(0,0,0,0.7)",
    borderRadius:8,
    fontSize:24,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    fontWeight:"bold"
  },
  main_container:{
    marginTop:64,
  },
  main_title:{
    textAlign:"center",
    color:"#00C0AB",
    fontSize:24,
    fontWeight:"bold"
  },
  mode_container:{
    
  },
  mode_text:{
    color:"white",
    fontSize:16,
    fontWeight:"bold",
  },
  mode_icon:{
    fontSize:24,
    marginRight:8,
  },
  genre_header:{
    display: "flex",
    justifyContent: "center",
    marginTop:64,
  },
  genre_title:{
    color:"white",
    fontSize:24,
    fontWeight:"bold",
    marginLeft:8
  },
  genre_icon:{
    fontSize:24,
    marginTop:2
  },
  genre_date:{
    color:"white",
    fontSize:18,
  },
  genre_time:{
    color:"white",
    fontSize:15,
    marginRight:20,
  },
  genre_fac:{
    color:"white",
    fontSize:18,
    marginBottom:0
  },
}